// 当日最早时间
export function getTodayFirstDay() {
  var y = new Date().getFullYear(); // 获取年份
  var m = new Date().getMonth() + 1; // 获取月份
  var d = new Date().getDate(); // 获取当月最后一日
  m = m < 10 ? '0' + m : m; // 月份补 0
  d = d < 10 ? '0' + d : d; // 日数补 0

  return [y, m, d].join('-') + ' 00:00:00';
}

// 当日最早时间 传入指定时间戳
export function timestampGetTodayFirstDay(dataTime) {
  const time = new Date(dataTime);
  var y = time.getFullYear(); // 获取年份
  var m = time.getMonth() + 1; // 获取月份
  var d = time.getDate(); // 获取当月最后一日
  m = m < 10 ? '0' + m : m; // 月份补 0
  d = d < 10 ? '0' + d : d; // 日数补 0

  return [y, m, d].join('-') + ' 00:00:00';
}

// 当日最晚时间
export function getTodayLastDay() {
  var y = new Date().getFullYear(); // 获取年份
  var m = new Date().getMonth() + 1; // 获取月份
  var d = new Date().getDate(); // 获取当月最后一日
  m = m < 10 ? '0' + m : m; // 月份补 0
  d = d < 10 ? '0' + d : d; // 日数补 0

  return [y, m, d].join('-') + ' 23:59:59';
}

