// 提前时间
export function getSearchToday(num) {
  var date = new Date(new Date().getTime() - (60 * 60 * 1000 * 24) * num);
  var y = date.getFullYear(); // 获取年份
  var m = date.getMonth() + 1; // 获取月份
  var d = date.getDate(); // 获取当月最后一日
  m = m < 10 ? '0' + m : m; // 月份补 0
  d = d < 10 ? '0' + d : d; // 日数补 0
  return [y, m, d].join('-');
}

// 提前时间 传入指定时间戳
export function timestampGetSearchToday(num, dataTime) {
  var date = new Date(dataTime - (60 * 60 * 1000 * 24) * num);
  var y = date.getFullYear(); // 获取年份
  var m = date.getMonth() + 1; // 获取月份
  var d = date.getDate(); // 获取当月最后一日
  m = m < 10 ? '0' + m : m; // 月份补 0
  d = d < 10 ? '0' + d : d; // 日数补 0
  return [y, m, d].join('-');
}

// 格式转换
export function getTimeFunc(param) {
  var date = new Date(param);
  var y = date.getFullYear(); // 获取年份
  var m = date.getMonth() + 1; // 获取月份
  var d = date.getDate(); // 获取当月最后一日
  m = m < 10 ? '0' + m : m; // 月份补 0
  d = d < 10 ? '0' + d : d; // 日数补 0

  return [y, m, d].join('-');
}

// 当月第一天
export function getStartTime() {
  var date = new Date();
  date.setDate(1);// 将当前时间的日期设置成第一天
  var year = date.getFullYear(); // 得到当前年份
  let month = date.getMonth() + 1;// 得到当前月份（0-11月份，+1是当前月份）
  month = month > 10 ? month : '0' + month;// 补零
  var day = date.getDate();// 得到当前天数，实际是本月第一天，因为前面setDate(1) 设置过了
  day = day < 10 ? '0' + day : day; // 日数补 0

  // console.log(month) 03
  return year + '-' + month + '-' + day;// 这里传入的是字符串
};

// 当月最后一天
export function getEndTime() {
  var date = new Date();
  var year = date.getFullYear();
  var month = date.getMonth() + 1;
  month = month > 10 ? month : '0' + month;// 补零
  // 这里传入的是整数时间，返回的是下个月的第一天，因为月份是0-11
  var nextMonthFirthDay = new Date(year, month, 1);// 下个月的第一天
  console.log(nextMonthFirthDay);
  var oneDay = 1000 * 60 * 60 * 24;// 一天的时间毫秒数
  var endDay = new Date(nextMonthFirthDay - oneDay);
  var day = endDay.getDate(); // 本月最后一天
  console.log(day);
  day = day < 10 ? '0' + day : day; // 日数补 0

  // 这里传入的是字符串格式的时间，返回的是传入字符串的时间
  return year + '-' + month + '-' + day;
};
