<template>
  <div class="kanban_page">
    <div class="title">设备状态</div>
    <div class="table_wra">
      <table id="stateTable">
        <tr>
          <th>设备名称</th>
          <th style="color: #24FFFF">运行时长（分钟）</th>
          <th style="color: #FFF719">空闲时长（分钟）</th>
          <th style="color: #C0BFBF">离线时长（分钟）</th>
          <th style="color: #FF5353">告警时长（分钟）</th>
          <th>效率（%）</th>
        </tr>
        <tr v-for="(item, index) in devStateList" :key="index">
          <td>{{ item.deviceName }}</td>
          <td style="color: #24FFFF">
            {{ item.deviceLiveReportVo.runTimeSum }}
          </td>
          <td style="color: #FFF719">
            {{ item.deviceLiveReportVo.idleTimeSum }}
          </td>
          <td style="color: #C0BFBF">
            {{ item.deviceLiveReportVo.offlineTimeSum }}
          </td>
          <td style="color: #FF5353">
            {{ item.deviceLiveReportVo.warnTimeSum }}
          </td>
          <td>{{ item.deviceLiveReportVo.availability }}</td>
        </tr>
      </table>

      <div class="light"></div>
    </div>
  </div>
</template>

<script>
import { fontSize } from '@/utils/common';
import { getDeviceStateReportStatistics } from '@/api/kanban';
import { STATUS_CODE } from '@/utils/constantsFile';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import { getTodayFirstDay, getTodayLastDay } from '@/utils/publicFunction2';
export default {
  name: 'distribute',
  data() {
    return {
      kanbanConfig: {},
      devStateList: [],
      current_s: 1,
      size_s: 7
    };
  },
  created() {
    document.documentElement.style.fontSize = fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = fontSize() + 'px';
    };
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      this.kanbanConfig =
        KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    this.showDevState();
    this.timer = setInterval(() => {
      this.showDevState();
    }, this.kanbanConfig?.time || 15000);
  },
  destroyed() {
    this.timer && clearInterval(this.timer);
  },
  methods: {
    async showDevState() {
      var condtion = {
        secretKey: this.kanbanConfig.key
      };
      // if (!localStorage.getItem('companyId')) {
      //   condtion.secretKey = this.kanbanConfig.key;
      // }
      try {
        const res = await getDeviceStateReportStatistics(
          {
            startTime: getTodayFirstDay(),
            endTime: getTodayLastDay(),
            current: this.current_s,
            size: this.size_s
          },
          condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // console.log('设备状态', res);
          if (this.current_s * this.size_s >= res.total) {
            this.current_s = 1;
          } else {
            this.current_s++;
          }
          this.devStateList = res.rows;
        }
      } catch (error) {
        console.log(error);
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return (
        decodeURIComponent(
          (new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(
            url
          ) || ['', ''])[1].replace(/\+/g, '%20')
        ) || null
      );
    }
  }
};
</script>

