<template>
  <div class="kanban">
    <div class="titleHead">
      <p id="pHead">{{title}}</p>
      <p id="time">{{ time }}</p>
    </div>
    <div class="kanbanContent">
      <div class="kanbanDiv" ><indicator></indicator></div>
      <div class="kanbanDiv" ><distribute></distribute></div>
      <div class="kanbanDiv" ><deviceState></deviceState></div>
      <div class="kanbanDiv" ><deviceManage></deviceManage></div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';

import deviceManage from '@/views/kanban_bsb/deviceManage.vue';
import indicator from '@/views/kanban_bsb/indicator.vue';
import deviceState from '@/views/kanban_bsb/deviceState.vue';
import distribute from '@/views/kanban_bsb/distribute.vue';

export default {
  name: 'kanban',

  data() {
    return {
      title: '',
      time: ''
    };
  },
  components: {
    // 加载未审核组件
    deviceManage,
    indicator,
    deviceState,
    distribute
  },
  mounted() {

  },
  created() {
    var that = this;

    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }

    this.title = that.kanbanConfig.bgTitle;

    this.getDataTime();
  },
  methods: {
    // 看板时间
    getDataTime() {
      this.dataTime = new Date($.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')).getTime();
      console.log('时间', $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date'));
      this.toGetTime();
    },
    toGetTime() {
      setInterval(() => {
        var date = new Date(this.dataTime);
        var y = date.getFullYear();
        var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
        var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        this.time = y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
        this.dataTime += 1000;
      }, 1000);
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    }
  }
};
</script>

